import { createSlice } from '@reduxjs/toolkit';
import { IProductCard } from '../../components/shop/Shop';
import { getProducts } from './productsActions';

//!  redux    3  :
// 1. Store -  
// 2. Slice -    
// 3. Actions -    (  )

// *    action   slice

//     state
interface IProductState {
    products: IProductCard[],
    isLoading: boolean,
    error: string
}

// state  store   
const initialState: IProductState = {
    products: [],
    isLoading: false,
    error: '',
};

//          (products, user, ...)
//      createSlice ()
//              "" ()
export const productsSlice = createSlice({
    name: 'productsSlice', // 
    initialState, // ,     ()      

    //  
    reducers: {
        cleanProducts: (state) => {
            state.products = [];
        }
    },

    //  
    // 3  promise
    extraReducers: (builder) => {
        builder
            //     
            .addCase(getProducts.pending, (state) => {
                state.isLoading = true; // loader
            })
            //     
            .addCase(getProducts.fulfilled, (state, action) => {
                state.isLoading = false // loader
                state.products = action.payload; // payload -    state  
            })
            //     
            .addCase(getProducts.rejected, (state, action) => {
                state.isLoading = false
                state.products = [] //  
                state.error = action.payload as string //   
            })
            //        
            //.addCase(_.rejected, (state, action) => {
            //     state.isLoading = false
            //     state.products = [] //  
            //     state.error = action.payload as string //   
            // })
    },
});

export default productsSlice;
export const { cleanProducts } = productsSlice.actions
