
import styles from './productCard.module.css';
import MyButton from '../myButton/MyButton';
import { Link } from 'react-router-dom';

interface IProductCardProps { /* interface   ,       ,    - */
    id: number,
    title: string,
    price: number,
    description?: string,
    category?: string,
    image: string,
    rate?: number,
    count?: number,
    rating?: {
        rate: number,
        count: number
    }
}

function ProductCard_byTeacher({
    id,
    title,
    price,
    image,
    rating,
    count,
    rate
}: IProductCardProps) {

    return (
        <div key={id} className={styles.productCardGrid}>
            <h5>{title.length > 20 ? title.substring(0, 20) + '...' : title}</h5>
            <p className={styles.price}>{price} </p>
            <div className={styles.imgWrapper}>
                <img width={200} src={image} alt={title} />
            </div>
            <span> Rating: {rating?.rate}</span> {/*      .       ,  ,     ,        */}
            {count && <span> Count: {count}</span>}{/*        , 
                  */}
            <div>
                {/*   link    id      string,    url,     */}
                <Link to={String(id)}>
                    <MyButton name='about product' />
                </Link>
            </div>
            
        </div>
    )
}

export default ProductCard_byTeacher;
